/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background-color: #222;
  color: #fff; }

blockquote,
q {
  quotes: '' ''; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: ''; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul,
ol {
  margin: 0 0 1.5rem 3rem; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/*General Layout*/
.submit-div {
  display: flex;
  flex-flow: column nowrap;
  margin-top: 2rem; }

/* Buttons */
button {
  border: 2px solid #0c0;
  border-radius: 3px;
  background-color: #222;
  color: #0c0;
  font-size: 1rem;
  line-height: 1.5;
  padding: 0.6rem 1rem 0.4rem;
  margin-top: 2rem; }
  @media (min-width: 620px) {
    button {
      font-size: 1.25rem; } }
  @media (min-width: 1080px) {
    button {
      font-size: 1.25rem;
      justify-self: center; } }
  button:hover {
    border-color: #090;
    color: #090; }
  button:active, button:focus {
    border-color: #aaa #bbb #bbb; }

input[type='button'],
input[type='reset'],
input[type='submit'] {
  border: 2px solid #0c0;
  border-radius: 3px;
  background-color: #222;
  color: #0c0;
  font-size: 1rem;
  line-height: 1.5;
  padding: 0.6rem 1rem 0.4rem;
  margin-top: 2rem; }
  @media (min-width: 620px) {
    input[type='button'],
    input[type='reset'],
    input[type='submit'] {
      font-size: 1.25rem; } }
  @media (min-width: 1080px) {
    input[type='button'],
    input[type='reset'],
    input[type='submit'] {
      font-size: 1.25rem;
      justify-self: center; } }
  input[type='button']:hover,
  input[type='reset']:hover,
  input[type='submit']:hover {
    border-color: #090;
    color: #090; }

/* Fields */
input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'],
textarea {
  color: #fff;
  border: 2px solid #0c0;
  border-radius: 3px;
  background-color: #222;
  padding: 7px;
  width: 100%; }
  input[type='text']:focus,
  input[type='email']:focus,
  input[type='url']:focus,
  input[type='password']:focus,
  input[type='search']:focus,
  textarea:focus {
    color: #fff;
    border-color: #fff; }

textarea {
  padding-left: 3px;
  width: 100%; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00; }
  a:visited {
    color: #00cc00; }
  a:hover, a:focus, a:active {
    color: #090; }

/* Menus */
.main-navigation {
  display: flex; }
  .main-navigation ul {
    display: block;
    list-style: none;
    margin: 0;
    padding-left: 0;
    display: flex; }
  .main-navigation li {
    margin-right: 1rem;
    padding-right: 1rem;
    border-right: 1px solid #fff;
    line-height: 1.25rem; }

/* Small menu. */
.menu-toggle {
  display: none; }

/*Pagination Nav*/
.number-pagination {
  font-weight: 300;
  padding-top: 2rem; }
  .number-pagination a,
  .number-pagination span {
    margin-right: 0.5rem; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #222;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

