/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
/*All pages*/
.site {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  padding: 2.5%;
  position: relative; }
  .site .site-content {
    margin: 0; }
    @media (min-width: 620px) {
      .site .site-content {
        margin: 0; } }
    @media (min-width: 1080px) {
      .site .site-content {
        margin: 5% 15%;
        padding: 0 2%; }
        .site .site-content:before {
          content: '\f10d';
          left: 20px; }
        .site .site-content:after {
          content: '\f10e';
          right: 20px; }
        .site .site-content:before, .site .site-content:after {
          font-family: 'Font Awesome 5 Free';
          position: absolute;
          top: 175px;
          font-size: 125px;
          color: #0c0;
          font-weight: 900; } }

/*Home Page, Single Quote Page, Archive Pages (Categories, Tags)*/
